@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap");
html {
  --text: rgba(8, 32, 50, 1);
  --background: rgba(255, 255, 255, 1);
  --border: rgba(8, 32, 50, 1);
  --menu: rgba(238, 238, 238, 1);
  --grad: rgba(255, 255, 255, 0);
  max-width: 100%;
}
html[data-theme="dark"] {
  --text: rgba(255, 255, 255, 1);
  --background: rgba(17, 17, 17, 1);
  --border: rgba(255, 255, 255, 1);
  --menu: rgba(51, 51, 51, 1);
  --grad: rgba(1, 1, 1, 0);
  max-width: 100;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "DM Sans", sans-serif;
  font-size: 62.5%;
  color: var(--text);
}
body {
  max-width: 100%;
  background-color: var(--background);
  overflow-x: hidden;
}
h1 {
  font-size: 3.2rem;
  font-weight: 700;
  padding-top: 16px;
  padding-left: 20px;
}
h2 {
  font-size: 2.4rem;
  font-weight: 500;
  text-align: center;
  padding-bottom: 12px;
  padding-top: 8px;
}
p {
  font-size: 1.6rem;
  font-weight: 400;
  text-align: center;
}
.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #fab631;
}
.header svg {
  margin-top: 16px;
}
#hero {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.main-face--img {
  width: 100%;
  height: 100%;
  max-width: 600px;
  margin: 0 auto;
}
.main-face--img-container {
  position: relative;
}
.deg {
  position: absolute;
  height: 100px;
  width: 100%;
  bottom: 0;
  background: linear-gradient(0deg, var(--background) 40%, var(--grad));
  max-width: 600px;
}
.button {
  width: 160px;
  height: 40px;
  border: 3px solid var(--border);
  border-radius: 20px;
  display: block;
  margin: 8px auto;
  font-size: 1.6rem;
  font-weight: 700;
  background-color: transparent;
}
.button p {
  font-weight: 700;
}
button > a {
  font-size: 1.6rem;
  font-weight: 700;
}
.hero {
  position: relative;
  width: 95vw;
  top: -16px;
  max-width: 450px;
  margin: 0 auto;
}
.projects {
  background-color: #fab631;
  width: 98vw;
  border-radius: 40px;
  padding-bottom: 2px;
  max-width: 750px;
  margin: 0 auto;
}
.project-image-p1 {
  width: 90vw;
  max-width: 675px;
}
.project-image-p2,
.project-image-p3 {
  width: calc(45vw - 10px);
  padding-bottom: 8px;
  max-width: 327.5px;
}
.projects-image-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}
.pOct {
  max-width: 350px;
}
.tech-image-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  max-width: 500px;
  margin: 0 auto;
}
.tech-image-container img {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  margin: 12px 25px;
}
.footer-contact {
  max-width: 800px;
}
.footer-contact h2 {
  padding-bottom: 4px;
}
input,
textarea {
  width: 90vw;
  max-width: 700px;
  height: 40px;
  display: block;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 5px;
  border-radius: 20px;
  border: 3px solid var(--border);
  padding: 16px;
  font-size: 1.6rem;
  background-color: transparent;
}
::placeholder {
  color: var(--text);
  font-size: 1.6rem;
  text-align: left;
}
.large {
  height: 100px;
}
.submit {
  width: 50vw;
  padding: 2px;
  font-weight: 700;
  background-color: transparent;
  cursor: pointer;
}
#footer {
  position: relative;
}
.footer-social {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding-bottom: 8px;
  margin: 0 auto;
}
.footer-atr {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer-atr p {
  font-weight: 700;
  padding: 16px 10px;
}
a {
  text-decoration: none;
}
button {
  border: none;
  background: none;
}
.menu-hidden {
  position: absolute;
  top: 8px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  background-color: var(--menu);
}
.darkTheme > svg,
.lightTheme > svg,
.cancel > svg {
  margin-bottom: 16px;
}
a > p {
  font-weight: 700;
}
.check-container > input {
  width: 25px;
}
svg > path {
  fill: var(--text);
}
#cancel {
  stroke: var(--text);
}
.decoration-container-top {
  position: absolute;
  z-index: -1;
  width: 100vw;
}
.decoration-container-bottom {
  position: absolute;
  z-index: -1;
  width: 100vw;
  bottom: 0;
}
.decoration-top,
.decoration-bottom {
  width: 100%;
  height: 100%;
}
.decoration-top > path,
.decoration-bottom > path {
  fill: #fab631;
}
button.theme {
  background: #474a57;
  border-radius: 1000px;
  border: none;
  padding: 0;
  cursor: pointer;
  position: relative;
  outline: none;
  height: 42px;
  width: 80px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
button.theme::after {
  content: "";
  display: block;
  width: 42px;
  height: 42px;
  position: absolute;
  background: #f4f5f7;
  top: 0;
  left: 0;
  right: unset;
  border-radius: 100px;
  transition: 0.3s ease all;
  box-shadow: 0px 0px 2px 2px rgba(136, 114, 114, 0.2);
}
button.active {
  background: #fab631;
  color: #000;
}
button.active::after {
  right: 0;
  left: unset;
}
button.theme > svg {
  margin: 0;
}
.img-project-container {
  width: 80%;
  max-width: 300px;
}
.project-container {
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  border: 2px solid #eee;
  border-radius: 10px;
  margin: 0 auto;
  padding: 10px;
}
h3 {
  font-size: 1.6rem;
  font-weight: 700;
}
p {
  margin: 4px auto;
}
.project-image {
  border-radius: 20px;
  width: 100%;
}
.tech-used-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.tech-container {
  font-size: 1.4rem;
  border: 2px solid var(--border);
  border-radius: 10px;
  margin: 8px;
  padding: 4px;
}
.anchor-container {
  width: fit-content;
  margin: 0 auto;
}
.anchor-container > a {
  margin: 16px;
}
.description-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 450px;
}
#view-design {
  margin: 16px auto;
  font-weight: 300;
}
.contact-form-error {
  font-size: 1.6rem;
  display: block;
  text-align: center;
  margin: 0 auto;
  width: 90%;
  background-color: #fab631;
  padding: 8px;
  border-radius: 10px;
}
.none {
  display: none;
}
@media (min-width: 800px) {
  .container-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 800px;
    margin: 0 auto;
  }
  .footer-social {
    flex-direction: column;
    gap: 16px;
  }
}
@media (min-width: 1000px) {
  .menuButton {
    display: none;
  }
  .menu-hidden {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: transparent;
    max-width: 600px;
  }
  .cancel {
    display: none;
  }
  button.active {
    background: #aaa;
  }
}
